.slider {
  width: 200px;
  height: 400px;
  position: relative;
  overflow: hidden;
}

.slide-ana {
  height: 360px;
}

.slide-ana > div {
  width: 100%;
  height: 100%;
  position: absolute;
  transition: all 0.7s;
}

@media (min-width: 200px) and (max-width: 639px) {
  .slider {
    height: 300px;
    width: 170px;
  }
}

